body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.view-container{
  margin-top:20px;
}

.book-list .thumbnail img{
  width:100%;
}

.itemButton{
  margin-top: 20px;
  text-align: center;
}

.dropdown .btn-inverse{
  background:#50a8e1;
  border:1px solid #2d6898;
}
.dropdown .btn-inverse:hover{
  background:rgba(80,168,225,.9);
}
.btn-inverse{
  background-color: #363636;
  color: #fff;
  border-radius: 0;
}
.column{
  border-bottom:1px dashed #E5E5E5;
  display: -ms-flexbox;
  display: flex;
}

.column .ab-details-title{
  font-size: 1.2em;
  line-height: 50px;
  color: #666;
}
.ab-details-info{
 
  line-height: 55px;
  margin-left: 10px;
  font-size:12px;
  color: #777;
  text-shadow: 1px 1px 1px rgba(255, 255, 255, 1);
}  



